.todo-item {
  display: flex;
  justify-content: space-between;
  background: #f1f1f1;
  padding: 0.5rem 1rem;
  margin: 0.5rem 0;
  border-radius: 4px;
  align-items: center;
}

.todo-item span {
  cursor: pointer;
}

.todo-item .completed {
  text-decoration: line-through;
  color: gray;
}
